import ___CSS_LOADER_API_SOURCEMAP_IMPORT___ from "../../../../node_modules/css-loader/dist/runtime/cssWithMappingToString.js";
import ___CSS_LOADER_API_IMPORT___ from "../../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, ".action-link[data-v-02e1d1c5]{cursor:pointer}", "",{"version":3,"sources":["webpack://./resources/js/components/passport/Clients.vue"],"names":[],"mappings":"AA4YA,8BACA,cACA","sourcesContent":["<template>\n  <div>\n    <div class=\"card card-default\">\n      <div class=\"card-header bg-dark\">\n        <div\n          style=\"\n            display: flex;\n            justify-content: space-between;\n            align-items: center;\n          \"\n        >\n          <span> OAuth Clients </span>\n\n          <a class=\"action-link\" tabindex=\"-1\" @click=\"showCreateClientForm\">\n            Create New Client\n          </a>\n        </div>\n      </div>\n\n      <div class=\"card-body bg-dark\">\n        <!-- Current Clients -->\n        <p class=\"mb-0\" v-if=\"clients.length === 0\">\n          You have not created any OAuth clients.\n        </p>\n\n        <table class=\"table table-borderless mb-0\" v-if=\"clients.length > 0\">\n          <thead>\n            <tr>\n              <th>Client ID</th>\n              <th>Name</th>\n              <th>Secret</th>\n              <th></th>\n              <th></th>\n            </tr>\n          </thead>\n\n          <tbody>\n            <tr v-for=\"client in clients\" :key=\"client\">\n              <!-- ID -->\n              <td style=\"vertical-align: middle\">\n                {{ client.id }}\n              </td>\n\n              <!-- Name -->\n              <td style=\"vertical-align: middle\">\n                {{ client.name }}\n              </td>\n\n              <!-- Secret -->\n              <td style=\"vertical-align: middle\">\n                <code>{{ client.secret }}</code>\n              </td>\n\n              <!-- Edit Button -->\n              <td style=\"vertical-align: middle\">\n                <a class=\"action-link\" tabindex=\"-1\" @click=\"edit(client)\">\n                  Edit\n                </a>\n              </td>\n\n              <!-- Delete Button -->\n              <td style=\"vertical-align: middle\">\n                <a class=\"action-link text-danger\" @click=\"destroy(client)\">\n                  Delete\n                </a>\n              </td>\n            </tr>\n          </tbody>\n        </table>\n      </div>\n    </div>\n\n    <!-- Create Client Modal -->\n    <div\n      class=\"modal fade\"\n      id=\"modal-create-client\"\n      tabindex=\"-1\"\n      role=\"dialog\"\n    >\n      <div class=\"modal-dialog\">\n        <div class=\"modal-content\">\n          <div class=\"modal-header bg-dark\">\n            <h4 class=\"modal-title\">Create Client</h4>\n\n            <button\n              type=\"button\"\n              class=\"close\"\n              data-dismiss=\"modal\"\n              aria-hidden=\"true\"\n            >\n              &times;\n            </button>\n          </div>\n\n          <div class=\"modal-body bg-dark\">\n            <!-- Form Errors -->\n            <div class=\"alert alert-danger\" v-if=\"createForm.errors.length > 0\">\n              <p class=\"mb-0\"><strong>Whoops!</strong> Something went wrong!</p>\n              <br />\n              <ul>\n                <li v-for=\"error in createForm.errors\" :key=\"error\">\n                  {{ error }}\n                </li>\n              </ul>\n            </div>\n\n            <!-- Create Client Form -->\n            <form role=\"form\">\n              <!-- Name -->\n              <div class=\"form-group row\">\n                <label class=\"col-md-3 col-form-label\">Name</label>\n\n                <div class=\"col-md-9\">\n                  <input\n                    id=\"create-client-name\"\n                    type=\"text\"\n                    class=\"form-control\"\n                    @keyup.enter=\"store\"\n                    v-model=\"createForm.name\"\n                  />\n\n                  <span class=\"form-text text-muted\">\n                    Something your users will recognize and trust.\n                  </span>\n                </div>\n              </div>\n\n              <!-- Redirect URL -->\n              <div class=\"form-group row\">\n                <label class=\"col-md-3 col-form-label\">Redirect URL</label>\n\n                <div class=\"col-md-9\">\n                  <input\n                    type=\"text\"\n                    class=\"form-control\"\n                    name=\"redirect\"\n                    @keyup.enter=\"store\"\n                    v-model=\"createForm.redirect\"\n                  />\n\n                  <span class=\"form-text text-muted\">\n                    Your application's authorization callback URL.\n                  </span>\n                </div>\n              </div>\n            </form>\n          </div>\n\n          <!-- Modal Actions -->\n          <div class=\"modal-footer bg-dark\">\n            <button\n              type=\"button\"\n              class=\"btn btn-secondary\"\n              data-dismiss=\"modal\"\n            >\n              Close\n            </button>\n\n            <button type=\"button\" class=\"btn btn-primary\" @click=\"store\">\n              Create\n            </button>\n          </div>\n        </div>\n      </div>\n    </div>\n\n    <!-- Edit Client Modal -->\n    <div class=\"modal fade\" id=\"modal-edit-client\" tabindex=\"-1\" role=\"dialog\">\n      <div class=\"modal-dialog\">\n        <div class=\"modal-content\">\n          <div class=\"modal-header bg-dark\">\n            <h4 class=\"modal-title\">Edit Client</h4>\n\n            <button\n              type=\"button\"\n              class=\"close\"\n              data-dismiss=\"modal\"\n              aria-hidden=\"true\"\n            >\n              &times;\n            </button>\n          </div>\n\n          <div class=\"modal-body bg-dark\">\n            <!-- Form Errors -->\n            <div class=\"alert alert-danger\" v-if=\"editForm.errors.length > 0\">\n              <p class=\"mb-0\"><strong>Whoops!</strong> Something went wrong!</p>\n              <br />\n              <ul>\n                <li v-for=\"error in editForm.errors\" :key=\"error\">\n                  {{ error }}\n                </li>\n              </ul>\n            </div>\n\n            <!-- Edit Client Form -->\n            <form role=\"form\">\n              <!-- Name -->\n              <div class=\"form-group row\">\n                <label class=\"col-md-3 col-form-label\">Name</label>\n\n                <div class=\"col-md-9\">\n                  <input\n                    id=\"edit-client-name\"\n                    type=\"text\"\n                    class=\"form-control\"\n                    @keyup.enter=\"update\"\n                    v-model=\"editForm.name\"\n                  />\n\n                  <span class=\"form-text text-muted\">\n                    Something your users will recognize and trust.\n                  </span>\n                </div>\n              </div>\n\n              <!-- Redirect URL -->\n              <div class=\"form-group row\">\n                <label class=\"col-md-3 col-form-label\">Redirect URL</label>\n\n                <div class=\"col-md-9\">\n                  <input\n                    type=\"text\"\n                    class=\"form-control\"\n                    name=\"redirect\"\n                    @keyup.enter=\"update\"\n                    v-model=\"editForm.redirect\"\n                  />\n\n                  <span class=\"form-text text-muted\">\n                    Your application's authorization callback URL.\n                  </span>\n                </div>\n              </div>\n            </form>\n          </div>\n\n          <!-- Modal Actions -->\n          <div class=\"modal-footer bg-dark\">\n            <button\n              type=\"button\"\n              class=\"btn btn-secondary\"\n              data-dismiss=\"modal\"\n            >\n              Close\n            </button>\n\n            <button type=\"button\" class=\"btn btn-primary\" @click=\"update\">\n              Save Changes\n            </button>\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script lang=\"ts\">\nimport axios from \"axios\";\n\nexport default {\n  /*\n   * The component's data.\n   */\n  data() {\n    return {\n      clients: [],\n\n      createForm: {\n        errors: [],\n        name: \"\",\n        redirect: \"\",\n      },\n\n      editForm: {\n        errors: [],\n        name: \"\",\n        redirect: \"\",\n      },\n    };\n  },\n\n  /**\n   * Prepare the component (Vue 2.x).\n   */\n  mounted() {\n    this.prepareComponent();\n  },\n\n  methods: {\n    /**\n     * Prepare the component.\n     */\n    prepareComponent() {\n      this.getClients();\n\n      $(\"#modal-create-client\").on(\"shown.bs.modal\", () => {\n        $(\"#create-client-name\").focus();\n      });\n\n      $(\"#modal-edit-client\").on(\"shown.bs.modal\", () => {\n        $(\"#edit-client-name\").focus();\n      });\n    },\n\n    /**\n     * Get all of the OAuth clients for the user.\n     */\n    getClients() {\n      axios.get(\"/oauth/clients\").then((response) => {\n        this.clients = response.data;\n      });\n    },\n\n    /**\n     * Show the form for creating new clients.\n     */\n    showCreateClientForm() {\n      $(\"#modal-create-client\").modal(\"show\");\n    },\n\n    /**\n     * Create a new OAuth client for the user.\n     */\n    store() {\n      this.persistClient(\n        \"post\",\n        \"/oauth/clients\",\n        this.createForm,\n        \"#modal-create-client\"\n      );\n    },\n\n    /**\n     * Edit the given client.\n     */\n    edit(client) {\n      this.editForm.id = client.id;\n      this.editForm.name = client.name;\n      this.editForm.redirect = client.redirect;\n\n      $(\"#modal-edit-client\").modal(\"show\");\n    },\n\n    /**\n     * Update the client being edited.\n     */\n    update() {\n      this.persistClient(\n        \"put\",\n        \"/oauth/clients/\" + this.editForm.id,\n        this.editForm,\n        \"#modal-edit-client\"\n      );\n    },\n\n    /**\n     * Persist the client to storage using the given form.\n     */\n    persistClient(method, uri, form, modal) {\n      form.errors = [];\n\n      axios[method](uri, form)\n        .then((response) => {\n          this.getClients();\n\n          form.name = \"\";\n          form.redirect = \"\";\n          form.errors = [];\n\n          $(modal).modal(\"hide\");\n        })\n        .catch((error) => {\n          if (typeof error.response.data === \"object\") {\n            form.errors = window._.flatten(\n              window._.toArray(error.response.data.errors)\n            );\n          } else {\n            form.errors = [\"Something went wrong. Please try again.\"];\n          }\n        });\n    },\n\n    /**\n     * Destroy the given client.\n     */\n    destroy(client) {\n      axios.delete(\"/oauth/clients/\" + client.id).then((response) => {\n        this.getClients();\n      });\n    },\n  },\n};\n</script>\n\n<style scoped>\n.action-link {\n  cursor: pointer;\n}\n</style>\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
