___CSS_LOADER_EXPORT___.push([module.id, ".action-link[data-v-12db92c2]{cursor:pointer}", "",{"version":3,"sources":["webpack://./resources/js/components/passport/AuthorizedClients.vue"],"names":[],"mappings":"AAgGA,8BACA,cACA","sourcesContent":["<template>\n  <div>\n    <div v-if=\"tokens.length > 0\">\n      <div class=\"card card-default\">\n        <div class=\"card-header bg-dark\">Authorized Applications</div>\n\n        <div class=\"card-body bg-dark\">\n          <!-- Authorized Tokens -->\n          <table class=\"table table-borderless mb-0\">\n            <thead>\n              <tr>\n                <th>Name</th>\n                <th>Scopes</th>\n                <th></th>\n              </tr>\n            </thead>\n\n            <tbody>\n              <tr v-for=\"token in tokens\" :key=\"token\">\n                <!-- Client Name -->\n                <td style=\"vertical-align: middle\">\n                  {{ token.client.name }}\n                </td>\n\n                <!-- Scopes -->\n                <td style=\"vertical-align: middle\">\n                  <span v-if=\"token.scopes.length > 0\">\n                    {{ token.scopes.join(\", \") }}\n                  </span>\n                </td>\n\n                <!-- Revoke Button -->\n                <td style=\"vertical-align: middle\">\n                  <a class=\"action-link text-danger\" @click=\"revoke(token)\">\n                    Revoke\n                  </a>\n                </td>\n              </tr>\n            </tbody>\n          </table>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script lang=\"ts\">\nimport axios from \"axios\";\n\nexport default {\n  /*\n   * The component's data.\n   */\n  data() {\n    return {\n      tokens: [],\n    };\n  },\n\n  /**\n   * Prepare the component (Vue 2.x).\n   */\n  mounted() {\n    this.prepareComponent();\n  },\n\n  methods: {\n    /**\n     * Prepare the component (Vue 2.x).\n     */\n    prepareComponent() {\n      this.getTokens();\n    },\n\n    /**\n     * Get all of the authorized tokens for the user.\n     */\n    getTokens() {\n      axios.get(\"/oauth/tokens\").then((response) => {\n        this.tokens = response.data;\n      });\n    },\n\n    /**\n     * Revoke the given token.\n     */\n    revoke(token) {\n      axios.delete(\"/oauth/tokens/\" + token.id).then((response) => {\n        this.getTokens();\n      });\n    },\n  },\n};\n</script>\n\n<style scoped>\n.action-link {\n  cursor: pointer;\n}\n</style>"],"sourceRoot":""}]);
